<template>
  <img alt="Vue logo" src="./assets/1.jpg" width="500">
  <br>
  <button v-on:click="showData1">คลิกเพื่อดูชื่อ</button>
  <button @click="showData2">คลิกเพื่อดูนามสกุล</button>
  <h1>{{firstname}} {{lastname}}</h1>

  <h3>{{age}}</h3>
  <button @click.right="plus">เพิ่มอายุ</button>
  <button @click="minus">ลดอายุ</button>
  <br>
  <button @click.ctrl="pluss(5)">เพิ่มอายุ+5</button>
  <button @click.ctrl="pluss2(10)">ลดอายุ-10</button>
  <br>
  <br>
  <form>
    <label for="">ป้อนชื่อจริง : </label>
    <input type="text" v-on:input="setfirstname">
    <br>
    <label for="">ป้อนนามสกุล : </label>
    <input type="text" v-on:input="setlastname">
    <br>
    <label for="">ป้อนชื่อเล่น : </label>
    <input type="text" v-on:input="setnickname">
    <br>
    <label for="">ป้อนอายุ : </label>
    <input type="text" v-on:input="setage">
    <br>
    <label for="">ป้อนวัน/เดือน/ปีเกิด : </label>
    <input type="text" v-on:input="setb">
    <br>
    <label for="">ป้อนสาขา : </label>
    <input type="text" v-on:input="setbr">
    <br>
    <label for="">ป้อนห้อง : </label>
    <input type="text" v-on:input="setr">
    <br>
    <label for="">ป้อนคติประจำใจ : </label>
    <input type="text" v-on:input="seti">
    <br>
    <label for="">ป้อนสถานที่เรียนต่อ : </label>
    <input type="text" v-on:input="sets">
    <br>
    <label for="">ป้อนวิชาที่ไม่ชอบ : </label>
    <input type="text" v-on:input="setd">
    <br>
  </form>

  <h2>แสดงชื่อจริง : {{firstname1}}</h2>
  <h2>แสดงนามสกุล : {{lastname1}}</h2>
  <h2>แสดงชื่อเล่น : {{nickname}}</h2>
  <h2>แสดงอายุ : {{age1}}</h2>
  <h2>แสดงวัน/เดือน/ปีเกิด : {{b}}</h2>
  <h2>แสดงสาขา : {{br}}</h2>
  <h2>แสดงห้อง : {{r}}</h2>
  <h2>แสดงคติประจำใจ : {{i}}</h2>
  <h2>แสดงสถานที่เรียนต่อ : {{s}}</h2>
  <h2>แสดงวิชาที่ไม่ชอบ : {{d}}</h2>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      firstname : "wichittra",
      lastname : "Khampeera",
      age : 20,
      nickname : "",
      firstname1 : "",
      lastname1 : "",
      age1 : "",
      b : "",
      br: "",
      r : "",
      i : "",
      s : "",
      d : ""
    }
  },
  methods:{
    showData1(){
      alert(this.firstname)
    },
    showData2(){
      alert(this.lastname)
    },
    plus(){
      this.age++
    },
    pluss(val){
      this.age+=val
    },
    minus(){
      this.age--
    },
    pluss2(val){
      this.age-=val
    },
    setnickname(event){
      //console.log(event.target.value);
      this.nickname = event.target.value
    },
    setfirstname(event){
      this.firstname1 = event.target.value
    },
    setlastname(event){
      this.lastname1 = event.target.value
    },
    setage(event){
      this.age1 = event.target.value
    },
    setb(event){
      this.b = event.target.value
    },
    setbr(event){
      this.br = event.target.value
    },
    setr(event){
      this.r = event.target.value
    },
    seti(event){
      this.i = event.target.value
    },
    sets(event){
      this.s = event.target.value
    },
    setd(event){
      this.d = event.target.value
    }
  }
};
</script>

